$font-primary: 'Domine', serif; //300, 400
$font-secondary: 'Roboto', sans-serif; //400, 500, 700
$font-cursive: 'Lobster', cursive;

$color-text: #FFFFFF; //use opacity for med and low emphasis, 60% for less important, 38% for disabled
$color-black: #121212; //for black on white background or dark bg

$color-secondary-light: #ffb900;
$color-secondary: #ff7730;

$color-primary: #0c7b93;
$color-primary-light: #00a8cc;
$color-primary-dark-1: #27496d;
$color-primary-dark-2: #142850;

$color-primary-highlight: #99e1f1;

//media queries

@mixin respond-w($breakpoint) {
    @if $breakpoint == phone-4 {
        @media screen and (max-width: 21.5625em) { @content }; //345px
    }
    @if $breakpoint == phone-3 {
        @media screen and (max-width: 23.4375em) { @content }; //375px
    }
    @if $breakpoint == phone-2 {
        @media screen and (max-width: 26.25em) { @content }; //420px
    }
    @if $breakpoint == phone-1 {
        @media screen and (max-width: 29.6875em) { @content }; //475px
    }
    @if $breakpoint == tablet-3 {
        @media screen and (max-width: 32.5em) { @content }; //520px
    }
    @if $breakpoint == tablet-2 {
        @media screen and (max-width: 37.5em) { @content }; //600px
    }
    @if $breakpoint == tablet-1 {
        @media screen and (max-width: 51.875em) { @content }; //830px
    }
    @if $breakpoint == laptop-2 {
        @media screen and (max-width: 60.9375em) { @content }; //975px
    }
	@if $breakpoint == laptop-1 {
		@media screen and (max-width: 71.875em) { @content }; //1150px
    }
}

@mixin respond-h($breakpoint) {
    @if $breakpoint == phone-8 {
        @media screen and (max-height: 21.5625em) { @content }; //345px
    }
    @if $breakpoint == phone-7 {
        @media screen and (max-height: 23.75em) { @content }; //380px
    }
    @if $breakpoint == phone-6 {
        @media screen and (max-height: 26.25em) { @content }; //420px
    }
    @if $breakpoint == phone-5 {
        @media screen and (max-height: 29.6875em) { @content }; //475px
    }
    @if $breakpoint == phone-4 {
        @media screen and (max-height: 32.5em) { @content }; //520px
    }
    @if $breakpoint == phone-3 {
        @media screen and (max-height: 37.5em) { @content }; //600px
    }
    @if $breakpoint == phone-2 {
        @media screen and (max-height: 40.625em) { @content }; //650px
    }
    @if $breakpoint == phone-1 {
        @media screen and (max-height: 46.875em) { @content }; //750px
    }
}

@mixin ios($breakpoint) {
    @if $breakpoint == 8 {
        @media (width: 23.4375em) and (height: 41.6875em) { @content }; //375px by 667px, iphone 6/7/8
    }
    @if $breakpoint == plus {
        @media (width: 25.875em) and (height: 46em) { @content }; //414px by 736px, iphone 6/7/8 plus
    }
    @if $breakpoint == ix {
        @media (width: 23.4375em) and (height: 50.75em) { @content }; //375px by 812px, iphone-x
    }
    @if $breakpoint == se {
        @media (width: 20em) and (height: 35.5em) { @content }; //320px by 568px, iphone-se, iphone 2-5
    }
}