@import "variables";

*, *::after, *::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
} 



html { //starting mobile, going up
    font-size: 53%;
    @include respond-w(tablet-1) {
        font-size: 45%;
    }
    @include respond-h(phone-1) {
        font-size: 41%;
    }
    @include respond-h(phone-2) {
        font-size: 38%;
    }
}

body {
    box-sizing: border-box;
    background-color: $color-black;
    color: $color-text;
    font-family: $font-secondary;
}