@import "variables";
@import "mixins";

.options-container {
    @include fixedWindow;
    background: $color-black;
    overflow-y: scroll;

    animation: slideIn 400ms ease-out;

    z-index: 500;

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: linear-gradient(to bottom right,
            rgba($color-primary, 0.075),
            rgba($color-primary, 0.01),
            transparent
        );
    }

    &__list {
        position: absolute;
        display: block;
        top: 50%;
        left: 50%;
        width: 80%;
        transform: translate(-50%, -50%);
        list-style: none;
        margin-left: 0;
        padding-left: 0;
        font-size: 5.2rem;

        z-index: 1000;

        & > li {
            font-family: $font-primary;
            text-transform: capitalize;
            padding: 1.5rem 4rem;
            @include respond-h(phone-2) { padding: 0.75rem 3rem }
            @include ios(8) { padding: 0.5rem 2rem }
            @include ios(plus) { padding: 0.6rem 2.2rem }
            @include ios(ix) { padding: 0.5rem 2rem }
            @include ios(se) { padding: 0.4rem 2rem }
            margin-bottom: 1.5rem;
            background: linear-gradient(to bottom left,
                rgba($color-primary, 0.4),
                rgba($color-primary, 0.05),
                transparent
            );
            border-radius: 5px;
            border: 2px solid $color-primary;

            box-shadow: 0px 0px 18px 3px rgba($color-primary-light, 0.4);

            &:hover {
                cursor: pointer;
            }
        }
    }
}

@keyframes slideIn {
    0% {
        transform: translateY(80rem);
    }
    100% {
        transform: translate(0);
    }
}