@import "variables";
@import "mixins";

.drink-grid {
    @include fixedWindow;
    
    display: grid;
    grid-template-columns: repeat(5, 17%);
    //tablets
    @include respond-w(tablet-1) {
        grid-template-columns: repeat(4, 20%);
    }
    //for all phones
    @include respond-w(tablet-2) {
        grid-template-columns: repeat(3, 25%);
    }
    justify-content: space-around;
    justify-items: center;
    align-content: start;
    grid-auto-flow: row;
    grid-row-gap: 2.5%;

    padding-top: 2rem;
    padding-bottom: 4rem;

    overflow: hidden;
    overflow-y: scroll;

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background: linear-gradient(to bottom right,
            rgba($color-primary, 0.075),
            rgba($color-primary, 0.01),
            transparent
        );
    }

    .card {
        position: relative;
        width: 100%;
        height: 20rem;
        // @include ios(iphone-8) {
        //     height: 16rem;
        // }
        border-radius: 8px;
        border: 2px solid rgba($color-text, 0.6);
        overflow: hidden !important;

        box-shadow: -1px 2px 20px 1px rgba($color-primary-light, 0.4);

        &:hover {
            cursor: pointer;
        }
    }
}